/** @jsx React.DOM */var React = require('react/addons')
var DropdownList = require('../src/dropdowns/dropdown-list.jsx')
var Select = require('../src/select/select.jsx')
var Calendar = require('../src/calendar/calendar.jsx')
var DatePicker = require('../src/pickers/datepicker.jsx')
var NumberPicker = require('../src/pickers/numberpicker.jsx')
var ComboBox = require('../src/dropdowns/combobox.jsx')
var chance = new (require('chance'))
//var _ = require('lodash')

var App = React.createClass({displayName: 'App',

	getInitialState: function(){
		var list = generateList()
		return {
			data: list,
			dropdownValue: list[0],
			comboboxValue: list[0],
			selectValues: [],
			calDate: new Date,
			numberValue: 1
		}
	},

	render: function(){
		var self = this;
		
		function change(field, data) {
			var obj = {}

			obj[field] = data
			self.setState(obj)
			//console.log('example: set field: ' + field, data)
		}

		return (
			React.DOM.div({style: { fontSize: 14}}, 
				React.DOM.div({style: { width: 200}}, 
					React.DOM.section({className: "example", style: { marginBottom: 20}}, 
						DropdownList({
							isRtl: false, 
							id: "MyDropdownList", 
							data:  this.state.data, 
							textField: "name", 
							valueField: "id", 
							value:  this.state.dropdownValue, 
							onChange: change.bind(null, 'dropdownValue')})
					), 
					React.DOM.section({className: "example", style: { marginBottom: 20}}, 
						Calendar({
							id: "Calendar", 
							value:  new Date, 
							onChange: change.bind(null, 'calValue')})
					), 
					React.DOM.section({className: "example", style: { marginBottom: 20}}, 
					ComboBox({
							isRtl: false, 
							data:  this.state.data, 
							textField: "name", 
							valueField: "id", 
							value:  this.state.comboboxValue, 
							onChange: change.bind(null, 'comboboxValue')})
					), 
					React.DOM.section({className: "example", style: { marginBottom: 20}}, 
						Select({
							isRtl: false, 
							data:  this.state.data, 
							placeholder: "hi i am a placeholder", 
							textField: "name", 
							valueField: "id", 
							value:  this.state.selectValues, 
							onChange: change.bind(null, 'selectValues')})
					), 
					React.DOM.section({className: "example", style: { marginBottom: 20}}, 
						DatePicker({id: "AwesomeDatePicker", isRtl: false, value: this.state.calDate, onChange: change.bind(null, 'calDate')})
					), 
					React.DOM.section({className: "example", style: { marginBottom: 20}}, 
						NumberPicker({id: "AwesomeNumPicker", 
							isRtl: false, 
							min: 0, 
							max: 5, 
							format: "c", 
							value: this.state.numberValue, 
							onChange: change.bind(null, 'numberValue')})
					)
				), 
				React.DOM.div({className: "clearfix"}, 
					React.DOM.div({className: "c1", style: { float: 'left', width: 150, height: 200}}), 
					React.DOM.div({className: "c2", style: { float: 'left', width: 150, height: 200}}), 
					React.DOM.div({className: "c3", style: { float: 'left', width: 150, height: 200}}), 
					React.DOM.div({className: "c4", style: { float: 'left', width: 150, height: 200}}), 
					React.DOM.div({className: "c5", style: { float: 'left', width: 150, height: 200}}), 
					React.DOM.div({className: "c6", style: { float: 'left', width: 150, height: 200}}), 
					React.DOM.div({className: "c7", style: { float: 'left', width: 150, height: 200}}), 
					React.DOM.div({className: "c8", style: { float: 'left', width: 150, height: 200}})
				)
			)

		)
	},


})

React.renderComponent(
	  App()
	, document.body);


				
function generateList(){
	var arr = new Array(100)

	for(var i = 0; i < arr.length; i++)
		arr[i] = { id: i + 1, name: chance.name() }

	return arr
}


